import { Component,OnInit} from '@angular/core';
import { StudentService } from '../Service/user.service';
import { Global } from '../Shared/global';
import { IStudent } from '../Model/student';
import { Observable } from 'rxjs/Rx';

@Component({
  selector: 'my-app',
  templateUrl: './student-list.component.html'
  })
  export class StudentListComponent implements OnInit{
          students: IStudent[];
          //Grid Vars start
    columns: any[] = [
      {
          display: 'Guid',
          variable: 'Id',
          filter: 'text',
      },
      {
          display: 'Student Name',
          variable: 'StudentName',
          filter: 'text'
      },
      {
          display: 'Address',
          variable: 'Address',
          filter: 'text'
      },
      {
          display: 'GovtId',
          variable: 'GovtId',
          filter: 'text'
      },
      {
          display: 'YesId',
          variable: 'YesId',
          filter: 'text'
      }
  ];
  sorting: any = {
    column: 'SchoolName',
    descending: false
};
          constructor(private _studentservice:StudentService){
            
          }

          ngOnInit(): void
          {
              
                this.LoadUsers();
          }
          LoadUsers(): void {
                this._studentservice.get(Global.BASE_STUDENT_ENDPOINT)
                    .subscribe(students => { this.students = students;
                    }
                    );
        }


  
  }


