import { PipeTransform, Pipe, Injectable } from '@angular/core';
import { IStudent } from '../Model/student';

@Pipe({
    name: 'userFilter'
})

@Injectable()
export class UserFilterPipe implements PipeTransform {

    transform(value: IStudent[], filter: string): IStudent[] {
        filter = filter ? filter.toLocaleLowerCase() : null;
        return filter ? value.filter((app: IStudent) =>
            app.StudentName != null && app.StudentName.toLocaleLowerCase().indexOf(filter) != -1
            || app.Address != null && app.Address.toLocaleLowerCase().indexOf(filter) != -1
        ) : value;
    }

}


