import { NgModule }       from '@angular/core';
import { BrowserModule }  from '@angular/platform-browser';
import { FormsModule,ReactiveFormsModule }    from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { AppComponent }     from './app.component';
import  {RouterModule} from '@angular/router';
import {StudentListComponent} from './student/student-list.component';
import {StudentAddComponent} from './student/student-add.component';
import {StudentExportComponent} from './student/student-export.component';
import {StudentUpdateclassComponent} from './student/student-updateclass.component';

import {TeacherListComponent} from './Teacher/teacher-list.component';

import {Tabs} from './common/common-tabs.component';
import {Tab} from './common/common-tab.component';
 //import {MdTabsModule} from 'md-tabs/tabs';
 import { MaterialModule, MdNativeDateModule,MdInputModule    } from '@angular/material';
 
 import { DataGrid } from './shared/datagrid.component';
//  import { DataGridUtil } from './shared/datagrid.util';
 import { Format } from './shared/format';
 import { OrderBy } from './shared/orderby';
 import { UserFilterPipe } from './filter/user.pipe';
 import { SearchComponent } from './Shared/search.component';
 import { StudentService } from './Service/user.service';
 import { HttpModule } from '@angular/http';

 //import 'hammerjs';
@NgModule({
  imports: [
    HttpModule,
    MaterialModule,
    MdNativeDateModule,
    MdInputModule, 
   // NoConflictStyleCompatibilityMode,
   // MdTabsModule,
    //Tabs,
    //Tab,
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    RouterModule.forRoot([
      {path : 'student', component: StudentListComponent},
      {path: 'studentadd', component: StudentAddComponent},
      {path: 'studentexport', component: StudentExportComponent},
      {path: 'studentupdateclass', component: StudentUpdateclassComponent},

      {path : 'teacher', component: TeacherListComponent}

    ]),
  ],
  declarations: [
    DataGrid,
    //DataGridUtil,
    UserFilterPipe,
    Format,
    OrderBy,
    SearchComponent,
    AppComponent,
    Tabs,
    Tab,
    StudentListComponent,
    StudentAddComponent,
    StudentExportComponent,
    StudentUpdateclassComponent,
    TeacherListComponent
  ],
  providers: [
    StudentService
  ],
  bootstrap: [ AppComponent ]
})
export class AppModule {


}



