import { Component } from '@angular/core';

import {
  Router,
  // import as RouterEvent to avoid confusion with the DOM Event
  Event as RouterEvent,
  NavigationStart,
  NavigationEnd,
  NavigationCancel,
  NavigationError
} from '@angular/router';

@Component({
  selector: 'my-app',
  template: `
  
<md-toolbar color="primary">
            <span>NSMI</span>
            <span class="spacer"></span>
            <button md-icon-button [mdMenuTriggerFor]="menu" #menuTrigger1="mdMenuTrigger" 
            (mouseenter)="menuTrigger1.openMenu()">
            <md-icon>menue</md-icon>
            </button>
</md-toolbar>
<md-menu #menu="mdMenu" [overlapTrigger]="false">
<button md-menu-item  href="#" routerLink="/student">
<md-icon>list</md-icon>
Student List

</button>
<button md-menu-item  href="#" routerLink="/studentadd">
<md-icon>person_add</md-icon>

<span>Student Add</span>
</button>
<button md-menu-item  href="#" routerLink="/studentexport">
<md-icon>file_upload</md-icon>
<span>File Import</span>
</button>

<button md-menu-item  href="#" routerLink="/studentupdateclass">
<md-icon>class</md-icon>
Student Class
</button>

</md-menu>

   <router-outlet>
   <div class="loading-overlay" *ngIf="loading">
   <md-progress-bar mode="indeterminate"></md-progress-bar>
     </div>
   </router-outlet>
  `
})
export class AppComponent {
  loading: boolean = true;

  constructor(private router: Router) {
    
            router.events.subscribe((event: RouterEvent) => {
                this.navigationInterceptor(event);
            });
        }

   // Shows and hides the loading spinner during RouterEvent changes
   navigationInterceptor(event: RouterEvent): void {
    if (event instanceof NavigationStart) {
        this.loading = true;
    }
    if (event instanceof NavigationEnd) {
        setTimeout(() => { this.loading = false; }, 1000)
        // this.loading = false;
    }

    // Set loading state to false in both of the below events to hide the spinner in case a request fails
    if (event instanceof NavigationCancel) {
        this.loading = false;
    }
    if (event instanceof NavigationError) {
        this.loading = false;
    }
}
}



